export function groupBy<K, V>(list: Array<V>, keyGetter: (input: V) => K) {
    const map = new Map<K, Array<V>>();
    const data: any[] = [];
    list.forEach((item) => {
        const key = keyGetter(item);
        const collection = map.get(key);
        if (!collection) {
            map.set(key, [item]);
            data.push({ groupBy: key, data: map.get(key) });
        } else {
            collection.push(item);
        }

    });
    return data;
}