import { OnInit } from '@angular/core';
import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LayoutService } from '../shared/services/app.layout.service';
import { PageMasterModel } from '../shared/models/model';
import { groupBy } from '../shared/utilities';

@Component({
    selector: 'app-menu',
    templateUrl: './app.menu.component.html'
})
export class AppMenuComponent implements OnInit {
    model: any[] = [];

    constructor(public layoutService: LayoutService,
        private route: ActivatedRoute) {

        this.route.pathFromRoot[1].data.subscribe(r => {
            // Loaded Menus based on role permission
            const menus: PageMasterModel[] = r['user']?.role?.rolePagePermissionDtos ?? [];
            const groupedByModule = groupBy(menus as any[], menu => menu.module);

            if (!!groupedByModule.length) {
                this.model = groupedByModule.map(gm => {
                    const menu = {
                        label: gm.groupBy,
                        items: !gm.data.length ? [] : gm.data.sort((a: any, b: any) => a.menuOrder - b.menuOrder).map((m: any) => {
                            return {
                                label: m.pageName,
                                icon: m.icon,
                                routerLink: m.link,
                            };
                        })
                    };
                    return menu;
                });
            }
        });
    }

    ngOnInit() { }
}
