import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { ToastrService } from '../services/toastr-message.service';
import { UserService } from '../services/user.service';

@Injectable({
  providedIn: 'root'
})
export class UserResolver implements Resolve<any> {

  constructor(private userService: UserService, private authService: AuthService, private toastrService: ToastrService) { }

  resolve() {
    return new Promise<any>((resolve, reject) => {
      this.userService.getUserInfo().subscribe({
        next: (user => {
          resolve(user);
        }),
        error: (err => {
          this.authService.signOut();
          this.toastrService.error('Authenticated failed.');

          resolve({
            isAuthenticated: false
          });
        })
      });
    });
  }
}
