import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppLayoutComponent } from './layout/app.layout.component';
import { NotfoundComponent } from './shared/components/notfound/notfound.component';
import { UserResolver } from './shared/resolvers/user.resolver';

const routes: Routes = [
  {
    path: '', component: AppLayoutComponent,
    resolve: { user: UserResolver },
    children: [
      { path: '', loadChildren: () => import('./dashboard/dashboard.module').then(m => m.DashboardModule) },
      { path: 'driver', loadChildren: () => import('./components/driver/driver.module').then(m => m.DriverModule) },
      { path: 'user', loadChildren: () => import('./components/user/user.module').then(m => m.UserModule) },
      { path: 'vehicles', loadChildren: () => import('./master/vehicle/vehicle.module').then(m => m.VehicleModule) },
      { path: 'mapping', loadChildren: () => import('./mapping/driver-vechile-mapping/driver-vehicle-mapping.module').then(m => m.DriverVechileMappingModule) },
      { path: 'institution', loadChildren: () => import('./master/institution/institution.module').then(m => m.InstitutionModule) },
      { path: 'menus', loadChildren: () => import('./master/page-master/page-master.module').then(m => m.PageMasterModule) },
      { path: 'menu-permission', loadChildren: () => import('./master/menu-permission/menu-permission.module').then(m => m.MenuPermissionModule) },
      { path: 'role', loadChildren: () => import('./master/role/role.module').then(m => m.RoleModule) },
      { path: 'vehicle-routes', loadChildren: () => import('./master/vehicle-route/vehicle-route.module').then(m => m.VehicleRouteModule) },
      { path: 'vehicle-stops', loadChildren: () => import('./master/vehicle-stops/vehicle-stops.module').then(m => m.VehicleStopsModule) },

    ]
  },
  { path: 'auth', loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule) },
  { path: 'register', loadChildren: () => import('./auth/register/register.module').then(m => m.RegisterModule) },
  { path: 'notfound', component: NotfoundComponent },
  { path: '**', redirectTo: '/notfound' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled', anchorScrolling: 'enabled', onSameUrlNavigation: 'reload' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
